  src: url("../fonts/cormorantGaramond-Italic.woff2") format("woff2"), url("../fonts/cormorantGaramond-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "cormorantGaramond";
  src: url("../fonts/cormorantGaramond-MediumItalic.woff2") format("woff2"), url("../fonts/cormorantGaramond-MediumItalic.woff") format("woff");
  font-style: italic;
  font-weight: 600;
  font-display: swap; }

@font-face {
  font-family: "cormorantGaramond";
  src: url("../fonts/cormorantGaramond-SemiBold.woff2") format("woff2"), url("../fonts/cormorantGaramond-SemiBold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: swap; }

@font-face {
  font-family: "poppins";
  src: url("../fonts/poppins-Bold.woff2") format("woff2"), url("../fonts/poppins-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "poppins";
  src: url("../fonts/poppins-Medium.woff2") format("woff2"), url("../fonts/poppins-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: swap; }

@font-face {
  font-family: "poppins";
  src: url("/source/fonts/poppins-Regular.woff2") format("woff2"), url("/source/fonts/poppins-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

body {
  font-family: "poppins", "Arial", sans-serif;
  color: #1d1613;
  font-weight: 500;
  font-style: 16px;
  line-height: 29px;
  min-height: 100vh;
  min-width: 320px;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  overflow-x: hidden; }

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 115px;
  border-bottom: 1px solid #F6F5F4; }

.header__svg {
  margin-right: 10px;
  cursor: pointer; }
  .header__svg:hover, .header__svg:focus {
    opacity: 0.5; }

.header__input {
  border: none; }
  .header__input::placeholder {
    font-size: 14px;
    line-height: 21px;
    color: #9B9997; }

.header__login,
.header__cart {
  color: #1d1613;
  font-size: 14px;
  line-height: 21px; }

.header__login {
  margin-right: 35px; }

.header__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0; }

.header__item {
  padding: 0;
  padding: 18px 35px; }

.header__link {
  color: #1d1613; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px; }

.slogan {
  background-color: #A87B62;
  background-image: url("../img/index-img/background-main-desktop.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  min-height: 463px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-resolution: 2dppx) {
    .slogan {
      background-image: url("../img/index-img/background-main-desktop@2x.png"); } }
  @media (max-width: 1023px) {
    .slogan {
      background-image: url("../img/index-img/background-main-tablet.png"); } }
  @media (max-width: 1023px) and (min-resolution: 2dppx) {
    .slogan {
      background-image: url("../img/index-img/background-main-tablet@2x.png"); } }
  @media (max-width: 767px) {
    .slogan {
      background-image: url("../img/index-img/background-main-mobile.png"); } }
  @media (max-width: 767px) and (min-resolution: 2dppx) {
    .slogan {
      background-image: url("../img/index-img/background-main-mobile@2x.png"); } }

.slogan__title {
  margin: 0;
  text-align: center;
  font-family: "cormorantGaramond", "Arial", sans-serif;
  font-size: 45px;
  line-height: 59px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff; }

.slogan__text {
  display: block;
  font-weight: 400;
  font-size: 38px;
  line-height: 49px;
  font-style: italic;
  text-transform: lowercase; }
